___CSS_LOADER_EXPORT___.push([module.id, `.popup-detail {
  /* display: none; */
  position: fixed;
  z-index: 999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
}

.popup-detail-content {
  margin: 5% auto;
  min-height: 600px;
  width: 50%;
  overflow: hidden;
  border-radius: 10px;
  border: 1px solid #888;
  background-color: #f8f8f8;
}

.popup-detail header {
  text-align: center;
  padding: 12px;
  height: 60px;
  background-color: #2c79c6;
}

.popup-detail>article>header>h1 {
  color: #ffffff;
}

.popup-detail>article>section {
  padding: 15px 45px 45px;
}

.close {
  position: relative;
  top: -32px;
  color: #ffffff;
  border-radius: 50%;
  float: right;
  height: 44px;
  width: 44px;
  font-size: 34px;
  font-weight: bold;
  text-decoration: none;
  background-color: #F73131;
}

.close:hover {
  cursor: pointer;
}

.profile-detail-kegiatan {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: 20px auto;
}

.profile-detail-kegiatan>h2 {
  color: #2c79c6;
  font-size: 1.3rem;
}

.img-detail-kegiatan {
  object-fit: cover;
  height: 200px;
  width: 250px;
}

.waktu-detail-kegiatan {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  color: #2c79c6;
}

.deskripsi-detail-kegiatan {
  margin-bottom: 25px;
}

.deskripsi-detail-kegiatan>h3 {
  margin-bottom: 5px;
  color: #2c79c6;
}

.content_deskripsi-detail-kegiatan {
  margin-bottom: 15px;
}

.content_deskripsi-detail-kegiatan>span {
  font-weight: 600;
  font-size: 1rem;
  color: #2c79c6;
}`, "",{"version":3,"sources":["webpack://./src/styles/popup.css"],"names":[],"mappings":"AAAA;EACE,mBAAmB;EACnB,eAAe;EACf,YAAY;EACZ,OAAO;EACP,MAAM;EACN,WAAW;EACX,YAAY;EACZ,cAAc;EACd,oCAAoC;AACtC;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,UAAU;EACV,gBAAgB;EAChB,mBAAmB;EACnB,sBAAsB;EACtB,yBAAyB;AAC3B;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,YAAY;EACZ,yBAAyB;AAC3B;;AAEA;EACE,cAAc;AAChB;;AAEA;EACE,uBAAuB;AACzB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,cAAc;EACd,kBAAkB;EAClB,YAAY;EACZ,YAAY;EACZ,WAAW;EACX,eAAe;EACf,iBAAiB;EACjB,qBAAqB;EACrB,yBAAyB;AAC3B;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,SAAS;EACT,iBAAiB;AACnB;;AAEA;EACE,cAAc;EACd,iBAAiB;AACnB;;AAEA;EACE,iBAAiB;EACjB,aAAa;EACb,YAAY;AACd;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,cAAc;AAChB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,cAAc;AAChB;;AAEA;EACE,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;EAChB,eAAe;EACf,cAAc;AAChB","sourcesContent":[".popup-detail {\r\n  /* display: none; */\r\n  position: fixed;\r\n  z-index: 999;\r\n  left: 0;\r\n  top: 0;\r\n  width: 100%;\r\n  height: 100%;\r\n  overflow: auto;\r\n  background-color: rgba(0, 0, 0, 0.8);\r\n}\r\n\r\n.popup-detail-content {\r\n  margin: 5% auto;\r\n  min-height: 600px;\r\n  width: 50%;\r\n  overflow: hidden;\r\n  border-radius: 10px;\r\n  border: 1px solid #888;\r\n  background-color: #f8f8f8;\r\n}\r\n\r\n.popup-detail header {\r\n  text-align: center;\r\n  padding: 12px;\r\n  height: 60px;\r\n  background-color: #2c79c6;\r\n}\r\n\r\n.popup-detail>article>header>h1 {\r\n  color: #ffffff;\r\n}\r\n\r\n.popup-detail>article>section {\r\n  padding: 15px 45px 45px;\r\n}\r\n\r\n.close {\r\n  position: relative;\r\n  top: -32px;\r\n  color: #ffffff;\r\n  border-radius: 50%;\r\n  float: right;\r\n  height: 44px;\r\n  width: 44px;\r\n  font-size: 34px;\r\n  font-weight: bold;\r\n  text-decoration: none;\r\n  background-color: #F73131;\r\n}\r\n\r\n.close:hover {\r\n  cursor: pointer;\r\n}\r\n\r\n.profile-detail-kegiatan {\r\n  display: flex;\r\n  flex-direction: c