___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  scroll-behavior: smooth;
  padding: 0;
  margin: 0;
}

body {
  padding: 0;
  margin: 0;
  font-family: 'Ubuntu', sans-serif;
  background-color: #f8f8f8;
}

a {
  text-decoration: none;
  display: inline-block;
}

/** MAIN  */

main {
  width: 100%;
  min-height: 90vh;
  margin: 0 auto;
}

hr {
  width: 200px;
  height: 5px;
  color: #25405b;
  text-align: center;
  background-color: #25405b;
  border-radius: 3px;
  margin: 5px auto;
}

.shadow {
  box-shadow: 0 5px 10px rgba(154, 160, 185, 0.05), 0 15px 40px rgba(166, 173, 201, 0.2);
}

/* header */

.header {
  background-color: #f8f8f8;
  box-shadow: 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 999;
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: #ffffff;
}

.header li a {
  display: block;
  padding: 20px 20px;
  text-decoration: none;
  margin-left: 15px;
}

.header li a:hover,
.header .menu-btn:hover {
  background-color: #fbbc26;
}

.header .logo {
  display: block;
  float: left;
  font-size: 2em;
  padding: 10px 20px;
  text-decoration: none;
}

/* menu */

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height 0.5s ease-out;
}

/* menu icon */

.header .menu-icon {
  cursor: pointer;
  display: inline-block;
  float: right;
  padding: 28px 20px;
  position: relative;
  user-select: none;
}

.header .menu-icon .navicon {
  background: #333;
  display: block;
  height: 2px;
  position: relative;
  transition: background 0.2s ease-out;
  width: 18px;
}

.header .menu-icon .navicon:before,
.header .menu-icon .navicon:after {
  background: #333;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all 0.2s ease-out;
  width: 100%;
}

.header .menu-icon .navicon:before {
  top: 5px;
}

.header .menu-icon .navicon:after {
  top: -5px;
}

/* menu btn */

.header .menu-btn {
  display: none;
}

.header .menu-btn:checked~.menu {
  max-height: 600px;
  padding: 15px;
  background-color: #f8f8f8;
}

.header .menu-btn:checked~.menu-icon .navicon {
  background: transparent;
}

.header .menu-btn:checked~.menu-icon .navicon:before {
  transform: rotate(-45deg);
}

.header .menu-btn:checked~.menu-icon .navicon:after {
  transform: rotate(45deg);
}

.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:before,
.header .menu-btn:checked~.menu-icon:not(.steps) .navicon:after {
  top: 0;
}

.header__image {
  display: inline-block;
  vertical-align: middle;
  margin-right: 10px;
  padding: 0px;
  height: 60px;
}

.close__menu {
  display: none;
  border: 0;
  background-color: transparent;
  font-size: 24px;
  min-width: 44px;
  min-height: 44px;
}

.close__menu {
  font-size: 44px;
}

/** NAV  */

.nav {
  width: 100%;
  padding-top: 9px;
  padding-bottom: 9px;
  padding-left: 100px;
  padding-right: 100px;
  position: sticky;
  background-color: #f8f8f8;
  top: 0;
  overflow: hidden;
  box-shadow: 2px 1px 15px 0px rgba(0, 0, 0, 0.57);
}

.nav__item {
  color: #000000;
  transition: ease 0.5s;
  padding: 15px;
  display: inline-block;
  border-radius: 5px;
  text-align: center;
}

.nav__itemadmn {
  color: #f8f8f8;
  background-color: #2c79c6;
  font-weight: bold;
  transition: ease 0.5s;
  padding: 15px;
  display: inline-block;
  border-radius: 25px;
  text-align: center;
}

.nav__item:hover,
.nav__item:active,
.nav__item.active {
  color: #f8f8f8;
  background-color: #395220;
}

.nav__item:active,
.nav__item.active {
  color: #f8f8f8;
  background-color: #395220;
  box-shadow: 0px 0px 15px 4px rgba(0, 0, 0, 0.12) inset;
}

.nav__list {
  list-style-type: none;
  display: flex;
  margin-top: 6px;
  float: right;
}

.nav__list li {
  margin-left: 18px;
}

/** SKIP LINK */

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  background-color: #2c79c6;
  color: #f8f8f8;
  padding: 8px;
  z-index: 1000;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: bolder;
}

.skip-link:focus {
  top: 0;
}

/** banner */

.container,
.container-riwayat-agenda {
  background-color: #2c79c6;
  background-size: cover;
  width: 100%;
  display: grid;
  padding: 50px;
}

.container {
  grid-template-columns: 3fr 1fr;
  grid-template-rows: auto 0 5px;
  gap: 0 70px;
}

.container-riwayat-agenda {
  grid-template-columns: 1fr 3fr;
  gap: 0 60px;
}

.hero_title {
  font-size: 20px;
  line-height: 55px;
  color: #f8f8f8;
}

.hero_title>h1 {
  margin: 0;
}

.p h1 {
  margin: 0;
  position: center;
}

.hero_subtitle {
  color: #f8f8f8;
  padding-top: 30px;
  padding-bottom: 30px;
  line-height: 18px;
}

.exchange_button {
  display: block;
  max-width: 175px;
  margin-top: 30px;
  padding: 12px;
  font-size: 16px;
  color: #2c79c6;
  background-color: #f8f8f8;
  border-radius: 5px;
  text-align: center;
  border-style: none;
  transition: background-color 0.3s;
  font-family: 'Ubuntu';
}

.exchange_button:hover {
  background-color: #fbbc26;
  color: #f8f8f8;
}

.search-input {
  margin-top: 25px;
  width: 270px;
  height: 44px;
  padding: 20px;
  margin-right: 0;
  border-style: none;
  border-radius: 10px 0 0 10px;
}

.btn-search {
  position: relative;
  font-weight: 300;
  cursor: pointer;
  color: #2c79c6;
  background-color: #fbbc26;
  right: 4.5px;
  height: 44px;
  width: 70px;
  border-style: none;
  border-radius: 0 10px 10px 0;
}

.btn-search:active,
.close:active,
#submit-kegiatan:active,
#ikuti-kegiatan:active {
  transform: scale(0.97);
}

/* content */

.image_hero,
.image_hero_cek_kegiatan {
  grid-row-start: 1;
  grid-row-end: 4;
  grid-column-start: 2;
  grid-column-end: 3;
  margin-left: auto;
  display: block;
  /* Membuat gambar menjadi elemen blok */
}

.img-riwayat-kegiatan {
  object-fit: cover;
}

.desc_hero {
  margin: auto 0;
}

.image_hero {
  height: 350px;
}

.image_hero_cek_kegiatan {
  height: 250px;
}

.deskripsi_container {
  background-color: #2c79c6;
  border-radius: 10px;
  text-align: center;
  color: #f8f8f8;
  padding-top: 10px;
  padding-left: 100px;
  padding-right: 100px;
  padding-bottom: 50px;
  max-width: 100%;
  margin: 50px auto 50px auto;
}

.nama h2 {
  margin-bottom: 20px;
}

.layanan_container {
  width: 100%;
  max-width: 100%;
  margin: auto;
  margin-bottom: 25px;
}

.sub_layanan_title {
  padding: 50px;
  text-align: center;
  font-size: 15px;
  color: #25405b;
}

.sub_layanan {
  width: 100%;
  margin: auto auto 50px auto;
}

.image_layanan {
  width: 100%;
  border-radius: 5px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.layanan_title,
.heading_layanan_title {
  padding: 5px;
  text-align: center;
  font-size: 15px;
  color: #25405b;
}

.layanan_title {
  font-size: 1.5rem;
  margin: 8px auto;
}

.heading_layanan_title {
  font-size: 1.2rem;
  margin-bottom: 25px;
}

.deskripsi_layanan {
  text-align: center;
  margin-bottom: 15px;
}

.deskripsi_paragraf p {
  text-align: center;
  width: 100%;
}

.icon_center {
  display: inline-block;
  text-align: center;
  width: 100%;
}

.tukar_container {
  border-radius: 10px;
  width: 100%;
  margin: 0px auto auto auto;
}

.tukar {
  width: 85px;
  text-align: center;
  color: #f8f8f8;
  background-color: #395220;
  font-size: 40px;
  padding: 20px;
  border-radius: 5px;
  margin: auto;
}

.item-riwayat-kegiatan,
.item-agenda-kegiatan {
  display: grid;
  grid-template-columns: 1fr 5fr;
  gap: 20px;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 50px;
  background-color: #d9d9d9;
}

.list-img-riwayat-kegiatan,
.list-img-agenda-kegiatan {
  height: 100%;
  width: 300px;
  object-fit: cover;
}

.item-riwayat-article {
  padding: 20px;
}

.content-riwayat-article {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 15px;
  gap: 25px;
}

.riwayat-agenda-kegiatan>h2 {
  text-align: center;
  color: #2c79c6;
}

.riwayat-agenda-kegiatan {
  padding: 20px;
}

.riwayat-agenda-kegiatan>hr {
  background-color: #2c79c6;
  margin-bottom: 40px;
}

.item-riwayat-article>a>h3 {
  color: #2c79c6;
}

.waktu-kegiatan {
  display: flex;
  gap: 80px;
  color: #555555;
}

.icon-waktu-kegiatan {
  margin-right: 6px;
}

/* Agenda */

.list-agenda-kegiatan {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 0 25px;
}

.desc-kegiatan {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

.item-agenda-kegiatan {
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 1fr;
  gap: 0;
}

.list-img-agenda-kegiatan {
  height: 250px;
  width: 100%;
}

.waktu-kegiatan-agenda {
  display: flex;
  flex-direction: column;
  gap: 10px;
  color: #555555;
}

/* footer */

footer {
  margin-top: 15px;
  height: 380px;
  margin-bottom: 0;
  background-color: #2c79c6;
  color: #f8f8f8;
  width: 100%;
  padding: 100px;
}

.footer-container {
  height: 210px;
  display: grid;
  grid-template-columns: 4fr 0.8fr 1fr;
  width: 100%;
  border-bottom: 2px solid #f8f8f8;
  overflow: hidden;
