}

/* Membersihkan float */
.clearfix::after {
  content: '';
  display: table;
  clear: both;
}

.logo_footer {
  height: 65px;
}

.footer-links h2 {
  margin-bottom: 12px;
}

.footer-links li {
  list-style-type: none;
  background-color: #2c79c6;
}

.footer-links ul {
  list-style-type: none;
  background-color: #2c79c6;
}

.footer-links li a {
  text-decoration: none;
  color: #f8f8f8;
  margin-bottom: 10px;
}

.footer-links ul li a:hover {
  color: #fbbc26;
  transition: 0.5s;
}

.social-icon {
  width: 44px;
  height: 44px;
  margin: 10px 10px 0 0;
  text-align: center;
  color: #f8f8f8;
}

.bi {
  margin: 5px 0;
}

.copyright,
.deskripsi_footer {
  margin-top: 12px;
  width: 70%;
}

/* Halaman Tentang*/
.tentangkami {
  border-bottom: 2px solid #f8f8f8;
  display: inline-block;
  padding-bottom: 5px;
  color: #f8f8f8;
  text-align: center;
  margin-bottom: 20px;
}

.rectangle {
  width: 60%;
  /* Sesuaikan lebar sesuai kebutuhan Anda */
  height: 300px;
  /* Sesuaikan tinggi sesuai kebutuhan Anda */
  background-color: #afb5b9;
  /* Sesuaikan warna sesuai keinginan Anda */
  color: #fff;
  /* Sesuaikan warna teks sesuai keinginan Anda */
  text-align: center;
  line-height: 200px;
  /* Sesuaikan tinggi sesuai kebutuhan Anda */
  margin: auto;
  display: flex;
  border-radius: 8px;
}

/* Dashboard Admin */
.container-post-kegiatan {
  max-width: 900px;
  margin: 0 auto;
}

.deskripsi_container {
  padding: 50px;
}

.post-kegiatan {
  margin-top: 50px;
}

.input_section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  border-radius: 10px;
}

.input_section>h2 {
  text-align: center;
  margin-bottom: 10px;
  color: #2c79c6;
}

.input_section>form>.input {
  margin: 10px 0;
}

#submit-kegiatan,
#ikuti-kegiatan {
  background-color: #fbbc26;
  color: #000000;
  font-weight: 500;
  border: 0;
  border-radius: 5px;
  display: block;
  height: 44px;
  width: 100%;
  margin-top: 20px;
  padding: 8px;
  cursor: pointer;
}

.input_section>form>.input>input,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

.input_section>form>.input>label {
  color: #2c79c6;
  font-weight: bold;
}

.input-img_kegiatan {
  padding-bottom: 25px;
  border-bottom: 1px solid #d9d9d9;
}

.main-content-container {
  padding: 30px;
}

.team {
  text-align: center;
  margin-bottom: 20px;
}

.team_container {
  margin: 40px auto;
  display: flex;
  flex-wrap: wrap;
  min-height: 15rem;
  max-width: 60rem;
  justify-content: center;
  gap: 3rem;
}

.team_avatar {
  background-color: #d9d9d9;
  margin: auto;
  border: 1px solid black;
  border-radius: 10px;
  padding: 20px;
  text-align: center;
}

.icon_team {
  display: block;
  margin: auto;
  height: 175px;
}

.icon_medsos-avtr {
  width: 100%;
  align-content: center;
  text-align: center;
  display: block;
  margin-top: 10px;
  transition: transform 0.3s ease;
}

.medsos {
  width: 40px;
  height: 40px;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #fff;
  color: #2c79c6;
  border-radius: 50%;
  margin: 0 5px;
  line-height: 40px;
  transition: background-color 0.3s ease;
}

.medsos:hover {
  transform: scale(1.2);
  /* Perbesar elemen saat dihover */
  background-color: #2c79c6;
  color: #ffffff;
}

.nama_team {
  padding: 20px;
  text-align: center;
  font-size: 10px;
  color: #000000;
  width: 100%;
}

